"use client";
import { useEffect, useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { useRouter } from "next/navigation";
import {
  LayoutDashboard,
  Users,
  BookOpen,
  Calendar,
  ClipboardList,
  PlayCircle,
} from "lucide-react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import theme from "@/styles/theme";

/**
 * Admin Dashboard Overview Page
 * Consumes: theme.colors.primary, accent, surfaceElevated, textOnPrimary
 */
export default function AdminOverviewPage() {
  const router = useRouter();
  const [stats, setStats] = useState({
    activeSemester: "Loading...",
    totalStudents: 0,
    totalLecturers: 0,
    totalCourses: 0,
  });
  const [chartData, setChartData] = useState([]);
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  // ðŸ”¹ Fetch summary stats
  const fetchStats = async () => {
    try {
      const res = await fetch("/api/admin/overview", {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setStats(data.stats);
      setChartData(data.chartData);
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    fetchStats();
  }, []);

  return (
    <main className="max-w-7xl mx-auto p-6 min-h-screen">
      <h1 className="text-2xl font-bold text-primary mb-6 flex items-center gap-2">
        <LayoutDashboard size={22} /> Admin Overview
      </h1>

      {/* ðŸ”¹ Top Stats */}
      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4 mb-8">
        <Card className="rounded-xl shadow-md">
          <CardContent className="p-4 flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm">Active Semester</p>
              <h2 className="text-lg font-semibold text-primary">
                {stats.activeSemester}
              </h2>
            </div>
            <Calendar className="text-primary" size={24} />
          </CardContent>
        </Card>

        <Card className="rounded-xl shadow-md">
          <CardContent className="p-4 flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm">Students</p>
              <h2 className="text-lg font-semibold text-primary">
                {stats.totalStudents}
              </h2>
            </div>
            <Users className="text-primary" size={24} />
          </CardContent>
        </Card>

        <Card className="rounded-xl shadow-md">
          <CardContent className="p-4 flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm">Lecturers</p>
              <h2 className="text-lg font-semibold text-primary">
                {stats.totalLecturers}
              </h2>
            </div>
            <ClipboardList className="text-primary" size={24} />
          </CardContent>
        </Card>

        <Card className="rounded-xl shadow-md">
          <CardContent className="p-4 flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm">Courses</p>
              <h2 className="text-lg font-semibold text-primary">
                {stats.totalCourses}
              </h2>
            </div>
            <BookOpen className="text-primary" size={24} />
          </CardContent>
        </Card>
      </div>

      {/* ðŸ”¹ Quick Actions */}
      <div className="mb-8">
        <h2 className="text-lg font-semibold text-primary mb-3">Quick Actions</h2>
        <div className="flex flex-wrap gap-3">
          <Button
            onClick={() => router.push("/dashboard/admin/semester")}
            className="bg-primary text-on-primary min-h-[2.5rem]"
          >
            <PlayCircle size={16} className="mr-2" />
            Start New Semester
          </Button>
          <Button
            onClick={() => router.push("/dashboard/admin/approvals")}
            className="bg-accent text-on-primary min-h-[2.5rem]"
          >
            <ClipboardList size={16} className="mr-2" />
            Approve Results
          </Button>
          <Button
            onClick={() => router.push("/dashboard/admin/settings")}
            variant="outline"
            className="min-h-[2.5rem]"
          >
            System Settings
          </Button>
        </div>
      </div>

      {/* ðŸ”¹ Registration Stats Chart */}
      <Card className="rounded-xl shadow-md">
        <CardContent className="p-4">
          <h2 className="text-lg font-semibold text-primary mb-4">
            Course Registration Activity
          </h2>
          {chartData.length === 0 ? (
            <p className="text-gray-500 text-sm">No chart data available.</p>
          ) : (
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={chartData}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="month" />
                <YAxis />
                <Tooltip />
                <Bar dataKey="registrations" fill={theme.colors.primary} />
                <Bar dataKey="resultsPublished" fill={theme.colors.accent} />
              </BarChart>
            </ResponsiveContainer>
          )}
        </CardContent>
      </Card>
    </main>
  );
}
